@import '../../styles/variables';
@import '../../styles/placeholder';

.container {
  @extend %flex;

  width: 1184px;
  flex-direction: column;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  gap: 20px;
}

.aboutDescription {
  font-size: 25px;
  font-weight: $font-weight-bold;
}

.chooseBtn {
  @extend %title;
  @extend %flex;

  width: 274px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 20px;

  font-size: 25px;
  color: $primary-white;
  box-shadow: $primary-box-shadow;
  border-radius: 12px;
  border-color: $transparent;

  background: $accent-color;
  transition: scale $transition-duration, background-color $transition-duration;

  &:hover,
  &:focus {
    background-color: rgba($accent-hover-color, 0.9);
    scale: 1.1;
  }
}
